* {
  box-sizing: border-box;
}

html {
  font-size: x-large;
  font-family: 'Cormorant Garamond', serif;
  background-color: mistyrose;
  color: rgb(132, 132, 137);
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

figure {
  margin: 0;
}

figcaption {
  font-size: small;
  font-style: italic;
  text-align: right;
}